/* fuentes */
/* colors */
* {
  box-sizing: border-box;
  margin: 0%;
  padding: 0%; }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.footer {
  background-color: #4a4a4a;
  font-family: "Roboto Slab", serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 2rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
  line-height: 3rem; }

.button {
  background-color: white;
  width: 4rem;
  height: 4rem;
  border-radius: 2.2rem;
  transform: translateY(-4.5rem);
  position: relative; }

.button_link {
  position: absolute;
  top: 15%;
  right: 1rem; }

.box1 {
  order: 4;
  font-size: 1.4rem; }

.box_link {
  font-size: 1.6rem; }

.footer_link {
  text-decoration-line: none;
  color: white; }

/* fuentes */
/* colors */
.header {
  position: fixed;
  z-index: 3; }

.header_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  background: #f26002; }

.header_link {
  width: 0.08rem;
  height: 0.2rem;
  color: #f26002; }

.main {
  font-family: "Roboto Slab", serif;
  overflow-x: hidden; }

.section1 {
  width: 32rem;
  height: 52rem;
  background-image: url(../images/cover.jpeg);
  display: flex;
  flex-direction: column;
  justify-content: end;
  text-align: center;
  gap: 3rem;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover; }

.hero {
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  color: white;
  padding-bottom: 3rem; }

.hero_p {
  padding: 2rem;
  font-size: 1.6rem; }

.hero_div {
  background-color: white;
  border-radius: 50%;
  align-items: center;
  width: 3.3rem;
  height: 3.3rem;
  position: relative; }

.button_down {
  position: absolute;
  top: 25%;
  left: 25%; }

.title1 {
  font-size: 3rem;
  font-weight: bold;
  padding-bottom: 1rem;
  align-items: center;
  padding-left: 1rem; }

.section2 {
  width: 100vw;
  height: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  gap: 1.5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-top: 3rem;
  margin-bottom: 3rem; }

.p1 {
  color: #f26002;
  font-size: 1.5rem;
  font-family: Roboto-regular; }

.section2_title {
  font-size: 2.5rem;
  justify-content: left;
  font-weight: bold;
  font-family: RobotoSlab Bold; }

.p2 {
  color: #4a4a4a;
  font-size: 1.8rem;
  font-family: Roboto regular;
  line-height: 3rem; }

.comprar {
  border-radius: 40%;
  border: solid 0.2rem #f26002;
  width: 10rem;
  height: 4rem;
  text-align: center;
  position: relative;
  box-shadow: 0.5rem #f26002; }

.section2_button {
  font-size: 1.8rem;
  font-family: Roboto medium;
  position: absolute;
  top: 25%;
  left: 13%;
  text-decoration-line: none;
  color: #f26002;
  font-weight: bold; }

.section3 {
  background-color: #f2f2f2;
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 3rem;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-bottom: 3rem;
  font-family: RobotoSlab-bold; }

.section3_title1 {
  /*     grid-area: title;
 */
  margin-top: 2.9rem;
  font-size: 2.5rem;
  font-weight: bold; }

.section3_title2 {
  font-size: 2rem;
  font-weight: bold;
  padding-bottom: 1rem; }

.section3_p {
  font-family: Roboto Roboto-regular;
  font-size: 1.8rem; }

.empezar_ahora {
  /* grid-area: four; */
  transform: translate(25%);
  border-radius: 20%;
  background-color: #f26002;
  width: 15rem;
  height: 4rem;
  position: relative; }

.section3_button {
  text-decoration-line: none;
  color: white;
  font-family: Roboto-Medium;
  font-size: 2rem;
  position: absolute;
  left: 10%;
  bottom: 25%; }

html {
  font-size: 62.5%; }
